import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { AccountsTransactionsJourneyComponent } from './accounts-transactions-journey.component';
import { AccountsListComponent } from './views/accounts-list/accounts-list.component';
import { TransactionsListComponent } from './views/transactions-list.component';
import { AccountsDetailsTabComponent } from './views/accounts-details-tab/accounts-details-tab.component';
import { ProductSummaryAccountSelectorWidgetModule } from '@backbase/product-summary-account-selector-widget-ang';
import { ProductSummaryListWidgetModule } from '@backbase/product-summary-list-widget-ang';
import { ProductSummaryManageAccountsWidgetModule } from '@backbase/product-summary-manage-accounts-widget-ang';
import { TransactionsListWidgetModule } from '@backbase/transactions-list-widget-ang';
import { TransactionsCommonModule } from '@backbase/transactions-common-ang';
import { ProductSummaryDetailsWidgetModule } from '@backbase/product-summary-details-widget-ang';
import { AccountsDetailsComponent } from './views/accounts-details.component';
import { AccountsManageComponent } from './views/accounts-manage.component';
import { AccountsManageGuardService } from './accounts-manage-guard.service';
import { TransactionDetailsComponent } from './views/transactions-details.component';
import { TransactionsHelperService } from './transactions-details-helper.service';
import { AccountGraphicalHeaderModule, AccountHeaderModule } from '@backbase/product-summary-ui';
import { ValidationModule } from '@backbase/product-summary-common-ang';
import { AccountInfoViewComponent } from './views/account-info/account-info-view.component';
import { AccountInfoViewModule } from './views/account-info/account-info-view.module';
import { ArrangementsListComponent } from './views/arrangements-list/arrangements-list.component';

export const defaultRoute: Route = {
  path: '',
  component: AccountsTransactionsJourneyComponent,
  children: [
    {
      path: '',
      redirectTo: 'list',
      pathMatch: 'full',
    },
    {
      path: 'list',
      component: AccountsListComponent,
      data: { title: 'My Accounts' },
    },
    {
      path: 'manage',
      component: AccountsManageComponent,
      canActivate: [AccountsManageGuardService],
    },
    {
      path: 'transactions',
      component: AccountsDetailsTabComponent,
      data: { title: 'Transactions' },
      children: [
        { path: '', redirectTo: 'list', pathMatch: 'full' },
        {
          path: 'list',
          component: TransactionsListComponent,
          data: { title: 'List' },
          children: [
            {
              path: 'detail',
              component: TransactionDetailsComponent,
            },
          ],
        },
        {
          path: 'details',
          component: AccountInfoViewComponent,
          data: { title: 'Details' },
        },
      ],
    },
  ],
};

const views = [
  TransactionsListComponent,
  TransactionDetailsComponent,
  AccountsListComponent,
  ArrangementsListComponent,
  AccountsDetailsTabComponent,
  AccountsDetailsComponent,
  AccountsManageComponent,
];

@NgModule({
  declarations: [AccountsTransactionsJourneyComponent, ...views],
  imports: [
    CommonModule,
    BackbaseCoreModule.withConfig({
      classMap: {
        // eslint-disable-next-line
        AccountsTransactionsJourneyComponent,
      },
    }),
    RouterModule,
    AccountInfoViewModule,
    TransactionsListWidgetModule,
    TransactionsCommonModule,
    ProductSummaryAccountSelectorWidgetModule,
    ProductSummaryManageAccountsWidgetModule,
    ProductSummaryDetailsWidgetModule,
    ProductSummaryListWidgetModule,
    AccountHeaderModule,
    AccountGraphicalHeaderModule,
    ValidationModule,
    TransactionsListWidgetModule.forRoot({
      showDetails: false,
    }),
  ],
  providers: [AccountsManageGuardService, TransactionsHelperService],
})
export class AccountsTransactionsJourneyModule {
  static forRoot(
    data: { [key: string]: any; route: Route } = { route: defaultRoute },
  ): ModuleWithProviders<AccountsTransactionsJourneyModule> {
    return {
      ngModule: AccountsTransactionsJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
